import React, { useRef } from 'react'
import { connect } from 'react-redux'
import {actions} from './reducer'
export const FormContainer = ({addUser}) => {
    const postRef=useRef()
    const bodyRef=useRef()

    const handleSubmit=(e)=>{
        e.preventDefault()
        addUser(bodyRef.current.value,postRef.current.value,new Date().getTime())
        bodyRef.current.value=postRef.current.value=''
    }
    return (
        <form onSubmit={handleSubmit}>
        <div>
            <input type="text" ref={postRef} />
        </div>
        <div>
            <input type="text" ref={bodyRef} />
        </div>
        <div>
            <input type="submit" value='add Article'/>
        </div>


        
    </form>
    )
}

const mapStateToProps = (state) => ({
    
})  

const mapDispatchToProps =(dispatch)=> {
    
    return {
        addUser:(body,post,id)=>{dispatch({type:actions.ADD,payload:{article:{body,post,id}}})}
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(FormContainer)
